label {
  text-shadow: 2px 2px 4px rgba(72, 71, 71, .5);
  font-size: 14px;
  display: inline;
}

input[type="number"] {
  width: 100px;
  border: 1px solid #ccc;
  border-radius: 8px;
  margin-right: 20px;
  padding: 5px 15px;
  transition: box-shadow .3s, background-color .3s, color .3s, cursor .3s;
  box-shadow: 2px 2px 4px rgba(0, 0, 0, .2);
}

button[type="submit"] {
  cursor: pointer;
  text-shadow: 2px 2px 4px rgba(196, 188, 188, .5);
  background-color: #f0f0f0;
  border: 2px solid #212121;
  border-radius: 50px;
  margin-top: 10px;
  padding: 5px 20px;
  transition: box-shadow .3s, background-color .3s, color .3s, cursor .3s;
  box-shadow: 2px 2px 4px rgba(0, 0, 0, .2);
}

